import Layout from './layout'
import Link from 'next/link'
const data = <h1>Start with the routing</h1>
function Home()
{
    return <div>
        <Layout content = {data}>
           <Post title = "Hello next.js"></Post> 
        </Layout>
    </div>
}
function Post (props)
{
    return <div>
       <Link href = {'/post?title=${props.title}'}><a>{props.title}</a></Link>
    </div>
}
export default Home;